.page {
  font-family: 'Space Grotesk', sans-serif;
  font-size: 16px;
  line-height: 16px;
  color: $main-text-color;

  scroll-behavior: smooth;

  min-width: 320px;

  &__body {
    margin: 0;
  }

  &__menu {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;

    transition: all 0.3s;
    opacity: 0;
    transform: translateX(-100%);
    pointer-events: none;

    &:target {
      opacity: 1;
      transform: translateX(0);
      pointer-events: all;
    }
  }
}
